/// <reference path="../typings/typescriptapp.d.ts" />

"use strict";

describe("moveToY", () => {

    beforeEach(() => {
        angular.mock.module("virtualIndexedListView");
    });

    var moveToY: VirtualIndexedListView.IMoveToY;
    var getY: VirtualIndexedListView.IGetY;

    beforeEach(inject(($injector: ng.auto.IInjectorService) => {
        moveToY = (<VirtualIndexedListView.IMoveToY> $injector.get("virtualIndexedListView.moveToY"));
        getY = (<VirtualIndexedListView.IGetY> $injector.get("virtualIndexedListView.getY"));
    }));

    it("should be defined", () => {
        expect(moveToY).toBeDefined();
    });

    it("should be element", () => {
        var element = angular.element("<div>hi</div>")[0];
        console.log($(element).css("transform"));
        var e = moveToY(element, 100);
        console.log($(e).css("transform"));
        expect(element).toBeDefined();
    });

});